* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.container__buttons {
  margin: 0, auto;
  text-align: center;
  padding: 50px;
}
.timer {
  display: flex;
}

.field {
  margin-top: 30px;
  margin-right: 15px;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.value {
  font-size: 50px;
  font-weight: 400;
}

.label {
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
}
